/**
 * @class
 * @name World
 * @author Evan Moon
 */
import * as $ from 'jquery';
import { KM } from 'src/constants';
import { Scenario } from 'src/lib/graphics/Scenario';
import { Star } from 'src/lib/astronomical/Star';
import DimensionService from 'src/lib/services/Dimension.service';
import { Scene, WebGLRenderer, AmbientLight, Vector3, Vector2, SphereBufferGeometry, MeshBasicMaterial, Mesh, Color, BackSide, TextureLoader, Raycaster, AxesHelper, GridHelper } from 'three';
import CameraManager from 'src/lib/managers/Camera.manager';
import ControlsManager from 'src/lib/managers/Controls.manager';
import { Ticker } from 'src/lib/helpers/Ticker';
var World = /** @class */ (function () {
    function World(rendererSelector) {
        this.centerVector = new Vector3();
        this.rendererSelector = rendererSelector;
        this.rendererWidth = window.innerWidth;
        this.rendererHeight = window.innerHeight;
        this.rendererRatio = window.devicePixelRatio;
        this.setRenderer(rendererSelector);
        this.stageSize = 50000;
        this.renderConfig = {
            antialias: true,
            preserveDrawingBuffer: true,
            alpha: true
        };
        this.date = new Date();
        this.scene = new Scene();
        this.currentSpaceTexture = 'milkyway';
        this.isPlaying = false;
    }
    World.prototype.setWindow = function (w, h) {
        this.rendererWidth = w;
        this.rendererHeight = h;
    };
    World.prototype.setDimension = function (_a) {
        var smallest = _a.smallest, largest = _a.largest;
        DimensionService.setLargestDimension(largest);
        this.stageSize = DimensionService.getScaled(largest);
        this.smallestSMA = smallest;
    };
    World.prototype.calcDimension = function (scenarioData) {
        var sma = {
            largest: 0,
            smallest: 0
        };
        if (scenarioData.system.type === 'starsystem') {
            var system = scenarioData.system;
            sma.largest = system.planets.reduce(function (a, b) {
                return (b.orbit && b.orbit.base.a > a) ? b.orbit.base.a : a;
            }, 0);
            sma.smallest = system.planets.reduce(function (a, b) {
                return (b.orbit && (!a || b.orbit.base.a < a)) ? b.orbit.base.a : a;
            }, 0);
        }
        else if (scenarioData.system.type === 'planetsystem') {
            var system = scenarioData.system;
            sma.largest = system.moons.reduce(function (a, b) {
                return (b.orbit && b.orbit.base.a > a) ? b.orbit.base.a : a;
            }, 0);
            sma.smallest = system.moons.reduce(function (a, b) {
                return (b.orbit && (!a || b.orbit.base.a < a)) ? b.orbit.base.a : a;
            }, 0);
        }
        else
            throw new Error('Please check the scenario type!: calcDimension::World');
        sma.largest *= KM;
        sma.smallest *= KM;
        return sma;
    };
    World.prototype.setRenderer = function (selector) {
        this.rendererDOMjQuery = $(selector);
        this.rendererDOM = this.rendererDOMjQuery[0];
        this.rendererWidth = this.rendererDOMjQuery.width();
        this.rendererHeight = this.rendererDOMjQuery.height();
    };
    World.prototype.setScenario = function (scenarioData) {
        var _this = this;
        this.date = scenarioData.startDate;
        this.startDate = scenarioData.startDate;
        this.setDimension(this.calcDimension(scenarioData));
        this.scenario = new Scenario(scenarioData);
        var planetBodies = this.scenario.getBodies();
        this.scene.add(planetBodies.center);
        planetBodies.others.forEach(function (planetBody) { return _this.scene.add(planetBody); });
        // 각 행성 위치에 맞게 오브젝트 배치
    };
    World.prototype.getScenario = function () {
        return this.scenario;
    };
    World.prototype.create = function () {
        var _this = this;
        if (!this.scenario) {
            console.error('You have to set any scenario first. use setScenario()');
        }
        // set renderer
        if (!this.renderer) {
            this.renderer = new WebGLRenderer(this.renderConfig);
        }
        else {
            console.error('There is no renderer!');
        }
        this.renderer.setSize(this.rendererWidth, this.rendererHeight);
        this.renderer.setPixelRatio(this.rendererRatio);
        this.renderer.setClearColor(0x222222, 1);
        // set basic light
        var light = new AmbientLight(0x111111);
        light.position.set(0, 0, 0);
        this.scene.add(light);
        // set raycaster
        this.raycaster = new Raycaster();
        // set mouse position to 0, 0, 0
        this.mousePos = new Vector2();
        this.rendererDOMjQuery.append(this.renderer.domElement);
        $(document).on('click', function (e) {
            _this.onClick(e);
        });
        this.initCamera();
        this.initControls();
        this.initSpacebox();
        // this.initHelper();
        this.ticker = new Ticker(this.scenario.getStartDate());
        this.ticker.setSecondsPerTick(this.scenario.getSecondsPerTick());
        this.ticker.setCalcPerTick(this.scenario.getCalcPerTick());
        this.setPlanets();
    };
    World.prototype.render = function () {
        var _this = this;
        var centerObject = this.scenario.system.getCenter();
        var cameraAbsolutePosition = this.CameraManager.getAbsolutePosition();
        this.controls.update();
        this.renderer.render(this.scene, this.currentCamera);
        if (centerObject instanceof Star) {
            var star = centerObject;
            var starPosition = this.centerVector;
            var flarePosition = cameraAbsolutePosition.clone().sub(starPosition).multiplyScalar(0.1);
            var flareSize = star.getScreenSizeRatio(cameraAbsolutePosition, this.CameraManager.getConfig().fov);
            star.setFlarePosition(flarePosition);
            star.setFlareSize(flareSize, this.rendererHeight);
            star.labelManager.updatePosition(star.compressedPos, this.currentCamera);
        }
        var bodies = this.scenario.getAstronomicalObjects();
        var epochTime = new Date(this.ticker.epochTime);
        if (this.isPlaying) {
            this.date = this.ticker.currentTime;
            var epochTime_1 = new Date(this.ticker.epochTime);
            bodies.center.moveRotating(epochTime_1);
        }
        var queue = bodies.others.map(function (planet) {
            return new Promise(function (resolve) {
                if (_this.isPlaying) {
                    planet.moveRotating(epochTime);
                    planet.setPositionByDate(epochTime);
                }
                planet.labelManager.updatePosition(planet.compressedPos, _this.currentCamera);
                resolve();
            });
        });
        return Promise.all(queue);
    };
    World.prototype.setSecondsPerTick = function (tick) {
        this.ticker.setSecondsPerTick(tick);
    };
    World.prototype.setCameraPosition = function (planetId) {
        this.CameraManager.setCameraPosition(this.scenario, this.cameraPos, planetId);
        this.cameraPos = planetId;
    };
    World.prototype.initCamera = function () {
        var aspect = this.rendererWidth / this.rendererHeight;
        this.CameraManager = new CameraManager(this.scene, this.stageSize, aspect);
        this.CameraManager.init();
        this.currentCamera = this.CameraManager.globalCamera;
        this.cameraPos = 'root';
        this.CameraManager.initCurrentCameraPosition();
    };
    World.prototype.initControls = function () {
        this.ControlsManager = new ControlsManager(this.currentCamera, this.rendererDOM);
        this.controls = this.ControlsManager.createControls();
    };
    World.prototype.initSpacebox = function () {
        var name = 'spacebox';
        var d = 6;
        var r = this.stageSize * d;
        var widthSegments = 60;
        var heightSegments = 40;
        var space = {
            geometry: new SphereBufferGeometry(this.stageSize * d, widthSegments, heightSegments),
            material: new MeshBasicMaterial({
                color: new Color(0xaaaaaa),
                side: BackSide,
                map: new TextureLoader().load("static/images/space/" + this.currentSpaceTexture + ".jpg")
            })
        };
        space.mesh = new Mesh(space.geometry, space.material);
        space.mesh.name = name;
        space.mesh.rotateX(90);
        if (this.scene.getObjectByName(name)) {
            this.scene.remove(this.scene.getObjectByName(name));
        }
        this.scene.add(space.mesh);
    };
    World.prototype.initHelper = function () {
        var axisHelper = new AxesHelper(this.stageSize);
        var gridHelper = new GridHelper(this.stageSize, this.stageSize / 10);
        gridHelper.rotation.x = (90 / 180) * Math.PI;
        this.scene.add(axisHelper, gridHelper);
    };
    World.prototype.setPlanets = function () {
        var _this = this;
        var epochTime = new Date(this.ticker.epochTime);
        if (!this.scenario) {
            throw new Error('There is no scenario: setPlanets::World');
        }
        var bodies3d = this.scenario.getBodies();
        this.scene.add(bodies3d.center);
        bodies3d.others.forEach(function (planet) { return _this.scene.add(planet); });
        var bodies = this.scenario.getAstronomicalObjects();
        bodies.others.forEach(function (planet) {
            planet.moveRotating(epochTime);
            planet.setPositionByDate(epochTime);
            planet.labelManager.updatePosition(planet.compressedPos, _this.currentCamera);
            planet.hasTrace = true;
        });
    };
    World.prototype.onClick = function (e) {
        e.preventDefault();
        this.mousePos.x = (e.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mousePos.y = -(e.clientY / this.renderer.domElement.clientHeight) * 2 + 1;
        // this.raycaster.setFromCamera(this.mousePos, this.camera);
        var intersects = this.raycaster.intersectObjects(this.scene.children, true);
    };
    return World;
}());
export { World };
