/**
 * @name Kepler.service
 * @author Evan Moon
 * @desc 케플러 방정식 서비스
 */
var KeplerService = /** @class */ (function () {
    function KeplerService() {
    }
    /**
     * @name getEccentricity
     * @desc 뉴턴랩슨메소드를 사용하여 Eccentricity값을 반환한다
     * 뉴턴랩슨메소드는 maxIter값 만큼만 재귀호출 된다.
     */
    KeplerService.prototype.getEccentricity = function (nextStep, x1, maxIter) {
        var x = 0;
        for (var i = 0; i < maxIter; i++) {
            x = x1;
            x1 = nextStep(x);
        }
        return x1;
    };
    /**
     * @name computeLaguerre
     * @desc http://adsbit.harvard.edu//full/1986CeMec..39..199C/0000206.000.html
     */
    KeplerService.prototype.computeLaguerre = function (x, f, f1, f2) {
        return x + (-5 * f / (f1 + Math.sign(f1) * Math.sqrt(Math.abs(16 * f1 * f1 - 20 * f * f2))));
    };
    /**
     * @name computed
     * @desc 이심률이 0.0 < e < 0.9인 타원일 경우
     */
    KeplerService.prototype.compute = function (e, M) {
        return function (x) {
            return x + (M + e * Math.sin(x) - x) / (1 - e * Math.cos(x));
        };
    };
    /**
     * @name computeWithLaguerre
     * @desc 이심률이 0.9 <= e < 1.0인 타원일 경우
     */
    KeplerService.prototype.computeWithLaguerre = function (e, M) {
        var _this = this;
        return function (x) {
            var sin = e * Math.sin(x);
            var cos = e * Math.cos(x);
            var f = x - sin - M;
            var f1 = 1 - cos;
            var f2 = sin;
            return _this.computeLaguerre(x, f, f1, f2);
        };
    };
    /**
     * @name computeWithLaguerreHyp
     * @desc 이심률이 1 이상인 포물선인 경우
     */
    KeplerService.prototype.computeWithLaguerreHyp = function (e, M) {
        var _this = this;
        return function (x) {
            var sin = e * Math.sinh(x);
            var cos = e * Math.cosh(x);
            var f = x - sin - M;
            var f1 = cos - 1;
            var f2 = sin;
            return _this.computeLaguerre(x, f, f1, f2);
        };
    };
    return KeplerService;
}());
var instance = new KeplerService();
export default instance;
