/**
 * @class
 * @name Label
 * @author Evan Moon
 */
import * as $ from 'jquery';
import { Vector3 } from 'three';
var Label = /** @class */ (function () {
    function Label(name, planetName) {
        this.centerVector = new Vector3();
        this.name = name + "-label";
        this.planetName = planetName;
        this.position = new Vector3();
        this.elementString = "<div class=\"planet-label\"><h3>" + planetName + "</h3></div>";
        this.elementDOM = $(this.elementString);
        this.elementDOM.appendTo($('#renderer'));
    }
    Label.prototype.updatePosition = function (position, camera) {
        this.position = position.clone();
        this.position2d = this.get2DPosition(camera);
        this.setElementPosition(this.position2d);
    };
    Label.prototype.setElementPosition = function (position2d) {
        var style = {
            transform: "translate(" + (position2d.x - 20) + "px, " + (position2d.y - 20) + "px) scale(" + position2d.z + ")",
            opacity: 1,
        };
        this.elementDOM.css(style);
    };
    Label.prototype.get2DPosition = function (camera) {
        var position = this.position.clone();
        var position2d = position.project(camera);
        var w = window.innerWidth;
        var h = window.innerHeight;
        position2d.x = (position2d.x + 1) / 2 * w;
        position2d.y = -(position2d.y - 1) / 2 * h;
        if (position2d.z < 1 && position2d.x > 0 && position2d.x < w && position2d.y > 0 && position2d.y < h) {
            var distance = camera.position.distanceTo(position);
            var span = Math.atan(camera.fov / 2) * distance;
            var factor = 1 / (1 + Math.log10(span));
            position2d.z = 0.3 + factor;
            return position2d;
        }
        else {
            return this.centerVector;
        }
    };
    return Label;
}());
export { Label };
